/**
 * @WordPress dependencies
 */
import { RawHTML } from '@wordpress/element';
import { RichText, useBlockProps } from '@wordpress/block-editor';

/**
 * @Internal dependencies
 */
import { LsIcon } from '@components';
import { migrateIconName } from '@helper/migrate';

/**
 * @others dependencies
 */
import classnames from 'classnames';

/**
 * export deprecated
 */
const blockName = 'ark-block-button';
export default [
	// 2.20でのアイコンリニューアルより前
	{
		supports: {
			anchor: true,
			className: false,
			reusable: false,
		},
		attributes: {
			content: {
				type: 'string',
				source: 'html',
				selector: '.ark-block-button__link > span',
			},
			style: {
				type: 'string',
				default: 'fill',
			},
			href: {
				type: 'string',
				source: 'attribute',
				selector: 'a',
				attribute: 'href',
			},
			isNewTab: {
				type: 'boolean',
				default: false,
			},
			rel: {
				type: 'string',
				source: 'attribute',
				selector: 'a',
				attribute: 'rel',
			},
			textColor: {
				type: 'string',
			},
			bgColor: {
				type: 'string',
			},
			bgGradient: {
				type: 'string',
			},
			hover: {
				type: 'string',
				default: 'bright',
			},
			icon: {
				type: 'string',
			},
			iconPos: {
				type: 'string',
				default: 'left',
			},
			radius: {
				type: 'string',
			},
			width: {
				type: 'object',
			},
			htmlTags: {
				type: 'string',
				source: 'html',
				selector: '.ark-block-button[data-has-html]',
				default: '',
			},
			// 本来ないが、2.20 以前のアイコンデータを引き継ぐために追加
			iconV1: {
				type: 'string',
				source: 'html',
				selector: '.ark-block-button__link[data-has-icon="1"]',
				default: '',
			},
		},
		migrate(attributes) {
			const newAttrs = {};
			if (attributes.icon) {
				newAttrs.icon = migrateIconName(attributes.icon);
				console.log('Button icon migrated 2:', newAttrs.icon);
			}

			return {
				...attributes,
				...newAttrs,
			};
		},
		save: ({ attributes }) => {
			const {
				style,
				href,
				isNewTab,
				rel,
				content,
				textColor,
				bgColor,
				bgGradient,
				htmlTags,
				icon,
				iconPos,
				hover,
				radius,
				width = {},
				iconV1,
			} = attributes;

			let oldSvg = iconV1 || '';
			oldSvg = oldSvg.replace(/<span.*?<\/span>/g, ''); // arkb の span にはクラスがあることに注意

			let theIcon = null;
			if (oldSvg) {
				// console.log('Button old icon:', oldSvg);
				theIcon = <RawHTML>{oldSvg}</RawHTML>;
			} else if (icon) {
				theIcon = <LsIcon icon={icon} className={`${blockName}__icon -${iconPos}`} />;
			}

			const hasHtml = !!htmlTags;

			const blockStyles = {
				'--arkb-btn-color--text': textColor || null,
				'--arkb-btn-color--bg': bgGradient || bgColor || null,
				'--arkb-btn-radius': radius || null,
			};

			// ブロックprops
			const blockProps = useBlockProps.save({
				className: classnames(`${blockName} is-btn-${style}`),
				'data-has-width': !!width?.pc || !!width?.tab || !!width?.mb || null,
				'data-hover': hover || null,
				'data-has-html': hasHtml ? '1' : null,
				style: blockStyles || null,
			});

			// タグ直接入力モード
			if (hasHtml) {
				return (
					<div {...blockProps}>
						<RawHTML>{htmlTags}</RawHTML>
					</div>
				);
			}

			/* eslint react/jsx-no-target-blank: 0 */
			return (
				<div {...blockProps}>
					<a
						href={href}
						rel={rel || null}
						target={isNewTab ? '_blank' : null}
						className={`${blockName}__link`}
						data-has-icon={icon ? '1' : null}
					>
						{'left' === iconPos && theIcon}
						<RichText.Content
							tagName='span'
							className={`${blockName}__text`}
							value={content}
						/>
						{'right' === iconPos && theIcon}
					</a>
				</div>
			);
		},
	},
	// CSS変数名変更前 v.2.13より前？
	{
		supports: {
			anchor: true,
			className: false,
			reusable: false,
		},
		attributes: {
			content: {
				type: 'string',
				source: 'html',
				selector: '.ark-block-button__link > span',
			},
			style: {
				type: 'string',
				default: 'fill',
			},
			href: {
				type: 'string',
				source: 'attribute',
				selector: 'a',
				attribute: 'href',
			},
			isNewTab: {
				type: 'boolean',
				default: false,
			},
			rel: {
				type: 'string',
				source: 'attribute',
				selector: 'a',
				attribute: 'rel',
			},
			textColor: {
				type: 'string',
			},
			bgColor: {
				type: 'string',
			},
			bgGradient: {
				type: 'string',
			},
			hover: {
				type: 'string',
				default: 'bright',
			},
			icon: {
				type: 'string',
			},
			iconPos: {
				type: 'string',
				default: 'left',
			},
			radius: {
				type: 'string',
			},
			width: {
				type: 'object',
			},
			htmlTags: {
				type: 'string',
				source: 'html',
				selector: '.ark-block-button[data-has-html]',
				default: '',
			},
			// 本来ないが、2.20 以前のアイコンデータを引き継ぐために追加
			iconV1: {
				type: 'string',
				source: 'html',
				selector: '.ark-block-button__link[data-has-icon="1"]',
				default: '',
			},
		},
		migrate(attributes) {
			const newAttrs = {};
			if (attributes.icon) {
				newAttrs.icon = migrateIconName(attributes.icon);
				console.log('Button icon migrated 1:', newAttrs.icon);
			}

			return {
				...attributes,
				...newAttrs,
			};
		},
		save: ({ attributes }) => {
			const {
				style,
				href,
				isNewTab,
				rel,
				content,
				textColor,
				bgColor,
				bgGradient,
				htmlTags,
				icon,
				iconPos,
				hover,
				radius,
				width = {},
				iconV1,
			} = attributes;

			let oldSvg = iconV1 || '';
			oldSvg = oldSvg.replace(/<span.*?<\/span>/g, ''); // arkb の span にはクラスがあることに注意

			let theIcon = null;
			if (oldSvg) {
				console.log('Button old icon:', oldSvg);
				theIcon = <RawHTML>{oldSvg}</RawHTML>;
			} else if (icon) {
				theIcon = <LsIcon icon={icon} className={`${blockName}__icon -${iconPos}`} />;
			}

			const hasHtml = !!htmlTags;

			const blockStyles = {
				'--arkb-btn-color': textColor || null,
				'--arkb-btn-bg': bgGradient || bgColor || null,
				'--arkb-btn-radius': radius || null,
			};

			// ブロックprops
			const blockProps = useBlockProps.save({
				className: classnames(`${blockName} is-btn-${style}`),
				'data-has-width': !!width?.pc || !!width?.tab || !!width?.mb || null,
				'data-hover': hover || null,
				'data-has-html': hasHtml ? '1' : null,
				style: blockStyles || null,
			});

			// タグ直接入力モード
			if (hasHtml) {
				return (
					<div {...blockProps}>
						<RawHTML>{htmlTags}</RawHTML>
					</div>
				);
			}

			/* eslint react/jsx-no-target-blank: 0 */
			return (
				<div {...blockProps}>
					<a
						href={href}
						rel={rel || null}
						target={isNewTab ? '_blank' : null}
						className={`${blockName}__link`}
						data-has-icon={icon ? '1' : null}
					>
						{'left' === iconPos && theIcon}
						<RichText.Content
							tagName='span'
							className={`${blockName}__text`}
							value={content}
						/>
						{'right' === iconPos && theIcon}
					</a>
				</div>
			);
		},
	},
];
