/**
 * @WordPress dependencies
 */
import {
	__, //_x
} from '@wordpress/i18n';
import { useState, useMemo, RawHTML } from '@wordpress/element';
import { registerBlockType } from '@wordpress/blocks';
import { RichText, BlockControls, InspectorControls, useBlockProps } from '@wordpress/block-editor';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import blockIcon from './icon';
import TheSidebar from './sidebar';
import TheToolbar from './toolbar';
import deprecated from './deprecated';
import { LsIcon, DynamicStyles } from '@components';
import { convertStylesToCssCode } from '@helper';

/**
 * @others dependencies
 */
import classnames from 'classnames';

/**
 * aタグにクラスを付与
 */
function addClassToAtag(aTag, addClass) {
	// すでに追加したいクラス名を持つかどうか
	const searchClassPattern = new RegExp(`${addClass}`);
	if (-1 !== aTag.search(searchClassPattern)) {
		return aTag;
	}

	// クラスを持つかどうか。 クオートコメントはPoeditのエラー回避用
	if (
		-1 !== aTag.search(/class=["']/) // '"
	) {
		return aTag.replace(/class=(["'])/, `class=$1${addClass} `); //'"
	}

	return aTag.replace('<a', `<a class="${addClass}"`);
}

/**
 * HtmlPreview
 */
const HtmlPreview = ({ html }) => {
	let prevHtml = html;

	// img タグ削除
	prevHtml = prevHtml.replace(/<img(.|\s)*?>/gim, '');

	// a タグを取得
	const aTagData = prevHtml.match(/<a.*?>/);

	// ひとつめのaタグにclassを当てる
	if (aTagData) {
		const newA = addClassToAtag(aTagData[0], `${blockName}__link`);
		prevHtml = prevHtml.replace(aTagData[0], newA);
	}

	return <RawHTML>{prevHtml}</RawHTML>;
};

/**
 * style生成
 */
function getDynamicStyles({ clientId, width }) {
	const styles = {
		all: {
			'--arkb-btn-width': width?.pc || '',
		},
		sp: {
			'--arkb-btn-width': width?.tab || '',
		},
		mb: {
			'--arkb-btn-width': width?.mb || '',
		},
	};

	return convertStylesToCssCode(`#block-${clientId}`, styles);
}

/**
 * ボタンブロック
 */
const blockName = 'ark-block-button';
registerBlockType(metadata.name, {
	title: __('Button', 'arkhe-blocks'),
	icon: blockIcon,
	edit: ({ attributes, setAttributes, clientId }) => {
		const {
			style,
			href,
			rel,
			isNewTab,
			content,
			textColor,
			bgColor,
			bgGradient,
			icon,
			iconPos,
			htmlTags,
			hover,
			radius,
			width,
		} = attributes;

		const [isLinkOpen, setIsLinkOpen] = useState(false);
		const hasHtml = !!htmlTags;

		// アイコン
		const iconTag = useMemo(() => {
			if (!icon) return null;
			return <LsIcon icon={icon} className={`${blockName}__icon -${iconPos}`} size='1em' />;
		}, [icon, iconPos]);

		const blockStyles = {
			'--arkb-btn-color--text': textColor || null,
			'--arkb-btn-color--bg': bgGradient || bgColor || null,
			'--arkb-btn-radius': radius || null,
		};

		// ブロックprops
		const blockProps = useBlockProps({
			className: `${blockName} is-btn-${style}`,
			'data-has-width': !!width?.pc || !!width?.tab || !!width?.mb || null,
			'data-hover': hover || null,
			'data-has-html': hasHtml ? '1' : null,
			style: blockStyles || null,
		});

		// CSS生成
		const dynamicStyles = getDynamicStyles({ clientId, width });

		return (
			<>
				{dynamicStyles && <DynamicStyles style={dynamicStyles} />}
				{!hasHtml && (
					<BlockControls group='block'>
						<TheToolbar
							attrs={{ ...{ href, rel, isNewTab, hasHtml } }}
							setAttributes={setAttributes}
							isLinkOpen={isLinkOpen}
							setIsLinkOpen={setIsLinkOpen}
						/>
					</BlockControls>
				)}
				<InspectorControls>
					<TheSidebar attrs={attributes} setAttributes={setAttributes} />
				</InspectorControls>
				<div {...blockProps}>
					{hasHtml ? (
						// HTML編集モード
						<HtmlPreview html={htmlTags} />
					) : (
						// 通常モード
						<>
							<div className={`${blockName}__link`} data-has-icon={icon ? '1' : null}>
								{'left' === iconPos && iconTag}
								<RichText
									tagName='span'
									className={`${blockName}__text`}
									placeholder={__('Enter text', 'arkhe-blocks') + '...'}
									value={content}
									withoutInteractiveFormatting // リンク書式OFF
									onChange={(newContent) => {
										setAttributes({ content: newContent });
									}}
								/>
								{'right' === iconPos && iconTag}
							</div>
						</>
					)}
				</div>
			</>
		);
	},

	save: ({ attributes }) => {
		const {
			style,
			href,
			isNewTab,
			rel,
			content,
			textColor,
			bgColor,
			bgGradient,
			htmlTags,
			icon,
			iconPos,
			hover,
			radius,
			width = {},
		} = attributes;

		const hasHtml = !!htmlTags;

		const blockStyles = {
			'--arkb-btn-color--text': textColor || null,
			'--arkb-btn-color--bg': bgGradient || bgColor || null,
			'--arkb-btn-radius': radius || null,
		};

		// ブロックprops
		const blockProps = useBlockProps.save({
			className: classnames(`${blockName} is-btn-${style}`),
			'data-has-width': !!width?.pc || !!width?.tab || !!width?.mb || null,
			'data-hover': hover || null,
			'data-has-html': hasHtml ? '1' : null,
			style: blockStyles || null,
		});

		// タグ直接入力モード
		if (hasHtml) {
			return (
				<div {...blockProps}>
					<RawHTML>{htmlTags}</RawHTML>
				</div>
			);
		}

		const iconTag = icon ? (
			<LsIcon icon={icon} className={`${blockName}__icon -${iconPos}`} />
		) : null;

		/* eslint react/jsx-no-target-blank: 0 */
		return (
			<div {...blockProps}>
				<a
					href={href}
					rel={rel || null}
					target={isNewTab ? '_blank' : null}
					className={`${blockName}__link`}
					data-has-icon={icon ? '1' : null}
				>
					{'left' === iconPos && iconTag}
					<RichText.Content
						tagName='span'
						className={`${blockName}__text`}
						value={content}
					/>
					{'right' === iconPos && iconTag}
				</a>
			</div>
		);
	},
	deprecated,
});
