import { createInterpolateElement } from '@wordpress/element';
import { addQueryArgs } from '@wordpress/url';

/**
 * 設定ページへのリンク付きテキストを生成。
 * text: <a>を含める。
 */
export default ({ tab, text = '' }) => {
	const helpLink = addQueryArgs('admin.php', {
		page: 'arkhe_blocks_settings',
		tab,
	});

	/* eslint jsx-a11y/anchor-has-content: 0 */
	return (
		<>
			{createInterpolateElement(text, {
				a: <a href={helpLink} target='_blank' rel='noopener noreferrer' />,
			})}
		</>
	);
};
