/**
 * @WordPress dependencies
 */
import { __, _x } from '@wordpress/i18n';
import { memo, useCallback } from '@wordpress/element';
import {
	PanelBody,
	BaseControl,
	ButtonGroup,
	Button,
	TextareaControl,
} from '@wordpress/components';
import { PanelColorGradientSettings } from '@compatible';

/**
 * @Internal dependencies
 */
import {
	ArkbMenuLink,
	ArkbProOnly,
	ArkbDeviceTab,
	ArkbUnitNumber,
	ArkbStyleControls,
	LsIconPicker,
} from '@components';

const IS_PRO = !!window.arkbSettings?.pfkey || false;

const STYLES = [
	{
		value: 'fill',
		label: _x('Fill', 'style', 'arkhe-blocks'),
	},
	{
		value: 'outline',
		label: _x('Outline', 'style', 'arkhe-blocks'),
	},
	{
		value: 'text',
		label: _x('Text', 'style', 'arkhe-blocks'),
	},
];

const HOVERS = [
	{
		value: 'bright',
		label: _x('Brighten', 'hover', 'arkhe-blocks'),
	},
	{
		value: 'dark',
		label: _x('Darken', 'hover', 'arkhe-blocks'),
	},
	{
		value: 'float',
		label: _x('Float', 'hover', 'arkhe-blocks'),
	},
	{
		value: 'swap',
		label: _x('Swap Color', 'hover', 'arkhe-blocks'),
	},
];

const WIDTH_LIST = ['25%', '50%', '75%', '100%'];

const Preview = memo(({ value = 'fill' }) => {
	return (
		<div className={`ark-block-button is-btn-${value}`}>
			<div className='ark-block-button__link'>
				<svg
					stroke='currentColor'
					fill='currentColor'
					strokeWidth='0'
					viewBox='0 0 512 512'
					className='ark-block-button__icon -left'
					height='1em'
					width='1em'
					xmlns='http://www.w3.org/2000/svg'
				>
					<path
						fill='none'
						strokeLinecap='round'
						strokeLinejoin='round'
						strokeWidth='48'
						d='M268 112l144 144-144 144m124-144H100'
					></path>
				</svg>
				<span className='ark-block-button__text'>Button</span>
			</div>
		</div>
	);
});

const PreviewHover = memo(({ label = 'Button', value = 'fill' }) => {
	return (
		<div className={`ark-block-button is-btn-fill`} data-hover={value}>
			<div className='ark-block-button__link'>
				<span className='ark-block-button__text'>{label}</span>
			</div>
		</div>
	);
});

const BtnWidthControl = memo(({ width, onChange, device }) => {
	return (
		<>
			<ArkbUnitNumber
				value={width[device] || '%'}
				max={{ px: 1000, other: 100 }}
				units={['px', 'em', 'rem', 'vw', '%']}
				onChange={(value) => {
					onChange(value, device);
				}}
				onClear={() => {
					onChange(undefined, device);
				}}
				widthRange={true}
			/>
			<ButtonGroup className='arkb-btns--small u-arkb--mt--5'>
				{WIDTH_LIST.map((_width) => {
					return (
						<Button
							text={_width}
							isPrimary={_width === width[device]}
							onClick={() => {
								onChange(_width, device);
							}}
							key={`iconkey_${_width}`}
						/>
					);
				})}
			</ButtonGroup>
		</>
	);
});

/**
 * サイドバー
 */
export default ({ attrs, setAttributes }) => {
	const {
		htmlTags,
		style,
		icon,
		iconPos,
		textColor,
		bgColor,
		bgGradient,
		hover,
		radius,
		width = {},
	} = attrs;

	const isTextStyle = 'text' === style;
	const hasHtml = '' !== htmlTags;

	// アイコン選択時の処理
	const setIcon = useCallback(
		(val) => {
			setAttributes({ icon: val || '' });
		},
		[setAttributes]
	);

	// カラーセット処理
	const setTextColor = useCallback(
		(newColor) => {
			setAttributes({ textColor: newColor });
		},
		[textColor]
	);
	const setBgColor = useCallback(
		(newColor) => {
			setAttributes({ bgColor: newColor || '' });
		},
		[setAttributes]
	);
	const setGradientColor = useCallback(
		(newGradient) => {
			setAttributes({ bgGradient: newGradient });
		},
		[setAttributes]
	);

	// カラー設定のデータ
	const colorPaletteData = [
		{
			colorValue: textColor,
			onColorChange: setTextColor,
			label: __('Text color', 'arkhe-blocks'),
		},
	];
	if (!isTextStyle) {
		colorPaletteData.push({
			colorValue: bgColor,
			gradientValue: bgGradient,
			onColorChange: setBgColor,
			onGradientChange: setGradientColor,
			label: __('Background color', 'arkhe-blocks'),
		});
	}

	const onChangeWidth = useCallback(
		(changedVal, device) => {
			const newValue = changedVal || undefined;

			const newWidth = { ...width };
			if (undefined === newValue) {
				delete newWidth[device];
			} else {
				newWidth[device] = newValue;
			}

			// 全て空かどうか
			if (!Object.keys(newWidth).length) {
				setAttributes({ width: undefined });
			} else {
				setAttributes({ width: newWidth });
			}
		},
		[setAttributes, width]
	);

	const onChangeStyle = useCallback((val) => {
		const newAttrs = { style: val };
		setAttributes({ ...newAttrs });
	}, []);

	const onChangeHoverStyle = useCallback((val) => {
		const newAttrs = { hover: val };
		setAttributes({ ...newAttrs });
	}, []);

	return (
		<>
			<PanelBody title={__('Style', 'arkhe-blocks')} initialOpen={true}>
				<ArkbStyleControls
					type='arkb-button'
					styles={STYLES}
					nowValue={style}
					setValue={onChangeStyle}
					defaultValue={STYLES[0].value}
					Preview={Preview}
					scale='0.6'
					height='40%'
				/>
				<div data-ark-disabled={isTextStyle} className='u-arkb--mt--15'>
					<BaseControl>
						<BaseControl.VisualLabel>
							{__('Hover effect', 'arkhe-blocks')}
						</BaseControl.VisualLabel>
						<ArkbStyleControls
							type='arkb-button--hover'
							styles={HOVERS}
							nowValue={hover}
							setValue={onChangeHoverStyle}
							defaultValue={HOVERS[0].value}
							Preview={PreviewHover}
							scale='0.6'
							height='50%'
						/>
					</BaseControl>
				</div>
			</PanelBody>
			<PanelColorGradientSettings
				title={__('Color settings', 'arkhe-blocks')}
				initialOpen={true}
				enableAlpha={true}
				settings={colorPaletteData}
			>
				<div className='arkb-helpText'>
					<ArkbMenuLink
						tab='block'
						text={__(
							'Default colors can be changed from the <a>Settings Page</a>.',
							'arkhe-blocks'
						)}
					/>
				</div>
			</PanelColorGradientSettings>
			<PanelBody title={__('Settings', 'arkhe-blocks')} initialOpen={true}>
				{!isTextStyle && (
					<BaseControl>
						<BaseControl.VisualLabel>
							{__('Rounded corners', 'arkhe-blocks')}
						</BaseControl.VisualLabel>
						<ArkbUnitNumber
							value={radius || 'px'}
							max={100}
							units={['px', 'em', 'rem', '%']}
							onChange={(value) => {
								setAttributes({ radius: value });
							}}
							onClear={() => {
								setAttributes({ radius: undefined });
							}}
							widthRange={true}
						/>
						<div className='arkb-helpText u-arkb--mt--5'>
							<ArkbMenuLink
								tab='block'
								text={__(
									'Default values can be changed from the <a>Settings Page</a>.',
									'arkhe-blocks'
								)}
							/>
						</div>
					</BaseControl>
				)}
				<BaseControl>
					<BaseControl.VisualLabel>
						{__('Button size', 'arkhe-blocks')}
					</BaseControl.VisualLabel>
					<ArkbDeviceTab
						className='-triple'
						controlPC={
							<BtnWidthControl width={width} onChange={onChangeWidth} device='pc' />
						}
						controlTab={
							<BtnWidthControl width={width} onChange={onChangeWidth} device='tab' />
						}
						controlMB={
							<BtnWidthControl width={width} onChange={onChangeWidth} device='mb' />
						}
					/>
				</BaseControl>
			</PanelBody>
			<PanelBody title={__('Icon', 'arkhe-blocks')} initialOpen={true}>
				<div data-ark-disabled={hasHtml}>
					<BaseControl>
						<BaseControl.VisualLabel>
							{__('Select Icon', 'arkhe-blocks')}
						</BaseControl.VisualLabel>
						<LsIconPicker value={icon} onChange={setIcon} clearable={true} />
					</BaseControl>
					{!!icon && (
						<BaseControl>
							<BaseControl.VisualLabel>
								{__('Icon position', 'arkhe-blocks')}
							</BaseControl.VisualLabel>
							<ButtonGroup className='arkb-btns--5em'>
								<Button
									text={__('Left', 'arkhe-blocks')}
									variant={'left' === iconPos ? 'primary' : 'secondary'}
									onClick={() => {
										setAttributes({ iconPos: 'left' });
									}}
								/>
								<Button
									text={__('Right', 'arkhe-blocks')}
									variant={'right' === iconPos ? 'primary' : 'secondary'}
									onClick={() => {
										setAttributes({ iconPos: 'right' });
									}}
								/>
							</ButtonGroup>
						</BaseControl>
					)}
				</div>
			</PanelBody>
			<PanelBody title={__('HTML direct input', 'arkhe-blocks')} initialOpen={true}>
				<ArkbProOnly>
					<TextareaControl
						help={__('You can paste tags for affiliate advertising.', 'arkhe-blocks')}
						value={htmlTags}
						rows='8'
						onChange={(theTag) => {
							if (!IS_PRO) return;
							setAttributes({ htmlTags: theTag });
							if ('' === theTag) {
								// タグをクリアした時
								setAttributes({ rel: '', content: '' });
							}
						}}
					/>
				</ArkbProOnly>
			</PanelBody>
		</>
	);
};
