/**
 * @WordPress dependencies
 */
// import { __ } from '@wordpress/i18n';
import { memo } from '@wordpress/element';
import { Button } from '@wordpress/components';

/**
 * @Internal dependencies
 */
import { setClass, hasClass, hasAnyClass } from '@helper';

/**
 * ArkbStyleControlItem
 */
const ArkbStyleControlItem = ({ idKey, selected, title, onClick, children }) => {
	return (
		<div className='arkb-styleControl__item'>
			<Button id={`arkb-styleControl-${idKey}`} onClick={onClick}></Button>
			<label
				htmlFor={`arkb-styleControl-${idKey}`}
				className='arkb-styleControl__inner'
				data-selected={selected}
			>
				<div className='arkb-styleControl__body'>
					<div className='arkb-styleControl__preview'>
						<div className='arkb-styleControl__preview__inner'>{children}</div>
					</div>
				</div>
				{title && <div className='arkb-styleControl__title'>{title}</div>}
			</label>
		</div>
	);
};

/**
 * ArkbStyleControls
 * 基本のclassName拡張: nowClass, setAttributes を受け取る
 * 独自attr: nowValue, setValue を受け取る
 */
export default memo(
	({
		type,
		styles,
		nowClass,
		setAttributes,
		nowValue,
		setValue,
		defaultValue = '',
		Preview,
		exData,
		scale,
		height,
		// clearable = false,
	}) => {
		let STYLE_CLASSES;

		const useClassAttr = !setValue;

		// スタイルのonclick処理
		if (useClassAttr) {
			STYLE_CLASSES = styles.map((style) => {
				return style.value;
			});
			setValue = (val) => {
				const newClass = setClass(nowClass, val, STYLE_CLASSES);
				setAttributes({ className: newClass });
			};
		}

		const styleProps = {};
		if (scale) {
			styleProps['--the-scale'] = `${scale}`;
		}
		if (height) {
			styleProps['--the-height'] = `${height}`;
		}

		// const clearBtn = clearable ? (
		// 	<div className='arkb-styleControls__clear'>
		// 		<Button
		// 			isSmall
		// 			isSecondary
		// 			text={__('Clear', 'arkhe-blocks')}
		// 			onClick={() => {
		// 				setValue('');
		// 			}}
		// 		/>
		// 	</div>
		// ) : null;

		return (
			<fieldset className={`arkb-styleControls for-${type}`} style={styleProps}>
				{styles.map((btn) => {
					let isSelected;
					if (useClassAttr) {
						// value 空の時は、他のどれも持ってないかをチェックする
						isSelected = btn.value
							? hasClass(nowClass, btn.value)
							: !hasAnyClass(nowClass, STYLE_CLASSES);
					} else {
						isSelected = btn.value === nowValue;
					}

					return (
						<ArkbStyleControlItem
							key={`${type}_${btn.value}`}
							idKey={`${type}--${btn.value}`}
							title={btn.label}
							selected={isSelected}
							onClick={() => {
								if (isSelected) {
									setValue(defaultValue);
								} else {
									setValue(btn.value);
								}
							}}
						>
							<Preview {...btn} exData={exData} />
						</ArkbStyleControlItem>
					);
				})}
				{/* {clearBtn} */}
			</fieldset>
		);
	}
);
